.settings_content_volume,
.settings_content_timeon,
.settings_content_time,
.settings_content_language {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: bisque;
  font-size: 24px;
  font-family: 'Arvo', serif;
}

.volume_slider {
  width: 250px;
  height: 4px;
  accent-color: bisque;
  cursor: pointer;
  margin-top: 20px;
}

.timeon_choose,
.language_choose {
  display: flex;
  column-gap: 20px;
}

.time_on,
.time_off,
.languages {
  cursor: pointer;
  opacity: 0.6;
}

.active {
  opacity: 1;
  border-bottom: 1px solid bisque;
}

.time_choose {
  display: flex;
  column-gap: 15px;
  margin-top: 20px;
}

.current_game_time {
  font-size: 20px;
  min-width: 35px;
  text-align: center;
}

.increment,
.decrement {
  width: 40px;
  font-size: 20px;
  border-radius: 10px;
  background-color: bisque;
  border: none;
  cursor: pointer;
  opacity: 0.7;
  transition: 0.3s ease-in-out;
}

.increment:hover,
.decrement:hover,
.language_text:hover {
  opacity: 1;
}

.settings_content_buttons {
  display: flex;
  column-gap: 30px;
}

.settings_content_language,
.settings_content_timeon {
  display: flex;
  row-gap: 10px;
}

.languages {
  margin: 0px;
}

@media screen and (max-width: 600px) {
  .settings_content_buttons {
    flex-direction: column;
    column-gap: 0px;
    row-gap: 20px;
  }
}