.button_style {
  min-width: 200px;
  background-color: transparent;
  border: 1px solid #ffe4c4;
  color: #ffe4c4;
  font-size: 16px;
  font-family: 'Arvo', serif;
  text-align: center;
  text-decoration: none;
  line-height: 40px;
  border-radius: 20px;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.button_style:hover {
  background-color: #FFBCA2;
  color: black;
}

@media screen and (max-width: 600px) {
  .button_style {
    min-width: 120px;
    line-height: 30px;
    border-radius: 20px;
  }
}